<template>
    <div class="other">
        <div class="other-nav">
            <div :class="['nav-item', active === index ? 'active' : '']" v-for="(item, index) in navList" :key="index" @click="handleSelect(item, index)">
                <span>{{item.title}}</span>
            </div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
import Bus from '../../tools/eventBus/index.js';

export default {
    data() {
        return {
            active: 0,
            navList: [
                {
                    title: '消息管理',
                    href: 'adminInfo'
                },
                {
                    title: '敏感操作日志',
                    href: 'sensiOperaLog'
                },
                {
                    title: '手动上下分',
                    href: 'manualUpDown'
                },
                {
                    title: '手动开奖',
                    href: 'manualLottery'
                },
                {
                    title: '账户安全',
                    href: 'accountPassword'
                },
                {
                    title: '外部飞单失败记录',
                    href: 'flyFailRecord'
                },
                // {
                //     title: '飞单管理',
                //     href: 'flyAlone'
                // }
                {
                    title: 'H5网页域名',
                    href: 'htmlDomain'
                }
            ]
        }
    },
    beforeRouteEnter (to, from, next) {
        // ...
        next(vm => {
            let path = to.name;

            vm.active = vm.navList.findIndex(item => item.href === path);
        })
    },
    methods: {
        handleSelect(item, index) {
            let that = this;

            if (that.active != index) {
                that.active = index;
                that.$router.push({ name: item.href });
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.other {
    .other-nav {
        display: flex;
        align-items: stretch;

        margin: 10px 0;

        height: 36px;

        background-color: #f2f2f2;
        border-radius: 4px;
        overflow: hidden;

        .nav-item {
            padding: 0 20px;

            line-height: 36px;

            cursor: pointer;
        }

        .active {
            background-color: rgb(65, 199, 219);
        }
    }
}
</style>